import { NgModule, ErrorHandler } from '@angular/core';
import { HttpModule } from "@angular/http";
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';
import { IonicStorageModule } from "@ionic/storage";
import { AdMobFree } from '@ionic-native/admob-free';
import { LocalNotifications } from '@ionic-native/local-notifications';
import { EmailComposer } from '@ionic-native/email-composer';


// import { PhotoViewer } from "@ionic-native/photo-viewer";

import { MoviesApi } from "../providers/api/movies-api";
import { SeriesApi } from "../providers/api/series-api";
import { ActorsApi } from "../providers/api/actors-api";
import { Api } from "../providers/api/api";

import { MoviesStorage } from "../providers/storage/movies-storage";
import { SeriesStorage } from "../providers/storage/series-storage";
import { ActorsStorage } from "../providers/storage/actors-storage";

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StreamingMedia } from '@ionic-native/streaming-media';
import { MoviesApiProvider } from '../providers/movies-api/movies-api';
import { TmdbProvider } from '../providers/tmdb/tmdb';
import { FavoriteProvider } from '../providers/favorite/favorite';
import { GlobalProvider } from '../providers/global/global';
import { EventLoggerProvider } from '../providers/event-logger/event-logger';
import { AuthProvider } from '../providers/auth/auth';

@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    BrowserModule,
    HttpModule,
    HttpClientModule,
    IonicStorageModule.forRoot(),
    IonicModule.forRoot(MyApp,{
      backButtonText: 'رجوع',
      backButtonIcon: 'ios-arrow-back',
      iconMode: 'md'
    }, 
{
  links: [
    { loadChildren: '../pages/actor-details/actor-details.module.ngfactory#ActorDetailsModuleNgFactory', name: 'ActorDetails', segment: 'actors/:id', priority: 'low', defaultHistory: ['Actors'] },
    { loadChildren: '../pages/cinemas/cinemas.module.ngfactory#CinemasPageModuleNgFactory', name: 'CinemasPage', segment: 'cinemas', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/actors/actors.module.ngfactory#ActorsModuleNgFactory', name: 'Actors', segment: 'actors', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/discover/discover.module.ngfactory#DiscoverModuleNgFactory', name: 'Discover', segment: 'discover', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/anime/anime.module.ngfactory#AnimePageModuleNgFactory', name: 'AnimePage', segment: 'anime', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/contactus/contactus.module.ngfactory#ContactusPageModuleNgFactory', name: 'ContactusPage', segment: 'contactus', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/favorites/favorites.module.ngfactory#FavoritesPageModuleNgFactory', name: 'FavoritesPage', segment: 'favorites', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/episode-details/episode-details.module.ngfactory#EpisodeDetailsPageModuleNgFactory', name: 'EpisodeDetailsPage', segment: 'episode-details', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/movie-details/movie-details.module.ngfactory#MovieDetailsModuleNgFactory', name: 'MovieDetails', segment: 'movies/:id', priority: 'low', defaultHistory: ['Movies'] },
    { loadChildren: '../pages/movies/movies.module.ngfactory#MoviesModuleNgFactory', name: 'Movies', segment: 'movies', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/movihome/movihome.module.ngfactory#MovihomePageModuleNgFactory', name: 'MovihomePage', segment: 'movihome', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomeModuleNgFactory', name: 'Home', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/register.module.ngfactory#RegisterPageModuleNgFactory', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/search/search.module.ngfactory#SearchModuleNgFactory', name: 'Search', segment: 'search', priority: 'low', defaultHistory: ['Home'] },
    { loadChildren: '../pages/serie-details/serie-details.module.ngfactory#SerieDetailsModuleNgFactory', name: 'SerieDetails', segment: 'series/:id', priority: 'low', defaultHistory: ['Series'] },
    { loadChildren: '../pages/seriehome/seriehome.module.ngfactory#SerieHomePageModuleNgFactory', name: 'SerieHomePage', segment: 'seriehome', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/series/series.module.ngfactory#SeriesModuleNgFactory', name: 'Series', segment: 'series', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/video-player/video-player.module.ngfactory#VideoPlayerPageModuleNgFactory', name: 'VideoPlayerPage', segment: 'video-player', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  
  providers: [
    StatusBar,
    SplashScreen,
    // PhotoViewer,
    MoviesApi,
    ActorsApi,
    SeriesApi,
    MoviesStorage,
    ActorsStorage,
    SeriesStorage,
    Api,
    StreamingMedia,
    AdMobFree,
    LocalNotifications,
    EmailComposer,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    MoviesApiProvider,
    TmdbProvider,
    FavoriteProvider,
    GlobalProvider,
    EventLoggerProvider,
    AuthProvider
  ]
})
export class AppModule {}



